const Helmet = ({ theme = {} }) => (
  <StaticQuery
    query={graphql`
      query HelmetQuery {
        dataJson {
          name
          description
          profilePictureUrl
          json_ld {
            Description
            _context
            _type
            address {
              addressLocality
              postalCode
              addressRegion
              addressCountry
            }
            birthDate
            email
            gender
            image
            name
            nationality
            sameAs
            url
            birthPlace {
              address {
                addressCountry
                addressRegion
                postalCode
                streetAddress
              }
            }
          }
        }
      }
    `}
    render={(data) => {
      let { name, description, profilePictureUrl, json_ld } = data.dataJson;
      let title = name;
      return (
        <ReactHelmet htmlAttributes={{ lang: 'en' }}>
          <meta charSet="utf-8" />
          <title>{title}</title>
          <meta name="description" content={description} />
          <link rel="shortcut icon" href={profilePictureUrl} />
          <meta name="theme-color" content={theme.background} />
          <meta name="image" content={profilePictureUrl} />
          <meta itemProp="name" content={title} />
          <meta itemProp="description" content={description} />
          <meta itemProp="image" content={profilePictureUrl} />
          <meta name="og:title" content={title} />
          <meta name="og:description" content={description} />
          <meta name="og:image" content={profilePictureUrl} />
          <meta name="og:site_name" content={title} />
          <meta name="og:locale" content="en_US" />
          <meta name="og:type" content="website" />
          <meta name="twitter:card" content="summary" />
          <meta name="twitter:title" content={title} />
          <meta name="twitter:description" content={description} />
          <meta name="twitter:image" content={profilePictureUrl} />
          <meta
            name="twitter:image:src"
            content={profilePictureUrl}
          />
          <script type="application/ld+json">{JSON.stringify(json_ld).replace('_type', '@type').replace('_context', '@context')}</script>
        </ReactHelmet>
      );
    }}
  />
);

